.row_poster{
    display: flex;
    flex-direction: row;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
    
}
.row_poster::-webkit-scrollbar {
    display: none;

}

.row_poster1{
    object-fit: contain;
    max-width: 200px;
    max-height: 170px;
    margin-right: 10px;
    transition: -webkit-transform 450ms;
    transition: transform 450ms ;
    transition: transform 450ms, -webkit-transform 450ms;
    z-index: -1;
    
}
.row_poster1:hover{
     -webkit-transform: scale(1.08);
             transform: scale(1.08);
}
/* .cards_overlay{
    position: absolute;
    padding: 0rem 1rem 5rem 1rem;
} */

.row_posterLarge{
        
    max-height: 250px;
    margin-right: 9px;
    transition: -webkit-transform 450ms;
    transition: transform 450ms ;
    transition: transform 450ms, -webkit-transform 450ms;
    z-index: -1;
    
}
.row_posterLarge:hover{
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
}
@-webkit-keyframes spinner {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes spinner {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  .loading-spinner {
    width: 50px;
    height: 50px;
    border: 10px solid #f3f3f3; /* Light grey */
    border-top: 10px solid #383636; /* Black */
    border-radius: 50%;
    -webkit-animation: spinner 1.5s linear infinite;
            animation: spinner 1.5s linear infinite;
    display: block;
    margin: auto;
  }
.banner{
    color: white;
    object-fit: contain;
    height: 600px;
    margin-bottom: 10px;
    margin-top: 50px;
}

#title{
    font-size: 2.5rem;
    font-weight: 700;
    padding-bottom: 0.3rem;
}

.container{
    padding-left: 30px;
    padding-top: 22rem;
    height: 180px;
    text-align: left;

}
.buttons{
    padding-top: 5px;
}
.button{
