.nav{
    display: flex;
    position: fixed;
    top: 0%;
    justify-content: space-between;
    width: 100%;
    height: 50px;
    
    z-index: 1;
    
    transition-timing-function: ease-in;
    transition: all 0.5s;
    align-items: center;
}

.nav_black{
    background-color: rgba(0,0,0,0.9);
    
}
.nav_logo{
    object-fit: contain;
    width: 80px;
    padding-left: 20px;
}

.nav_avatar{
    object-fit: contain;
    width: 60px;
    padding-right: 10px;
    padding-top: 10px;
}
span{
    color: white;
    font-weight:500;
    font-style: italic;
    padding: 5px;
    margin-left: 10px;
    margin-right: 10px;
}

.nav_center{
    display: flex;
    justify-content:left;
    align-items:center;
}
.movie__list {
    padding: 3rem 3rem 3rem 3rem;
}

.list__title {
font-size: 1.75rem;
margin:  2.5rem;
color: azure;
}

.list__cards {
    display: flex;
    flex-wrap: wrap ;
    justify-content: center;
}
.cards {
    display: inline-block;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    margin: 1rem;
    cursor:pointer;
    min-width:200px;
    height:300px;
    z-index:0;
    border: 1px solid rgb(99, 99, 99);
}

.cards:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    z-index: 1000;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.cards__img {
    height: 300px;
}

.cards__overlay {
    position: absolute;
    padding: 0 1rem 1rem 1rem;
    bottom: 0px;
    height: 290px;
    display: flex;
    flex-direction: column;
    width: 85%;
    justify-content: flex-end;
    background-image: linear-gradient(rgb(0,0,0,0), rgb(0,0,0,1));
    opacity: 0;
    transition: opacity .2s;
}   

.cards__overlay:hover {
    opacity: 1;
}

.card__title {
    font-weight: 900;
    font-size: 1rem;
    margin-bottom: 0.4rem;
}

.card__runtime {
    font-size: .75rem;
    /* margin-bottom: 0.25rem; */
}
.card__date{
    float: left;
    margin: 0px;
    /* padding: 0px; */
}

.card__rating {
    float: right;
    padding-bottom: 10px;
}
.card__rating > i{
    margin-left: 3px;
}

.card__description {
    font-style: italic;
    font-size: .75rem;
    margin-bottom: 0.25rem;
}
